.footer {
    background: #535353;
}

.copy {
    display: flex;
    min-height: 30px;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
    padding: 0 17px;

    color: #fff;
    font-size: 16px;
    line-height: 16px;
    font-family: 'Courier New', Courier, monospace;
    word-break: break-all;
}

.link {
    display: block;
    text-decoration: underline;
    cursor: pointer;
    color: #fff;
    word-break: break-all;
}

.link:hover {
    text-decoration: none;
}