.Header {
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    align-items: center;
    min-height: 30px;
    padding: 7px 0;
    gap: 27px;
    background: #535353;
    overflow: hidden;
}

.Header__right {
    margin: 0 0 0 auto;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
}

.NavLink {
    display: block;
    text-decoration: underline;
    cursor: pointer;
    color: #fff;
    user-select: none;

    font-family: 'Courier New', Courier, monospace;
    font-size: 20px;
    line-height: 20px;
    white-space: nowrap;
}

.NavLink__active {
    text-decoration: none;
    cursor: default;
    color: #fff;
}

.NavLink:hover {
    text-decoration: none;
}