.root {
    padding: 0 20px;
}

.logo {
    margin: 0 0 24px;
    padding: 20px 0 0;
    text-align: center;
    font-size: 42px;
    line-height: 42px;
    font-weight: bold;
    text-shadow: 4px 4px 7px #000;
}

.h1 {
    margin: 0 0 20px;
    padding: 20px 0 0;
    text-align: center;
    font-size: 32px;
    line-height: 32px;
    font-weight: bold;
}

.features {
    list-style: decimal;
    list-style-position: outside;
    margin: 0 0 30px 40px;
    padding: 0;
}

.features>li {
    margin: 0;
    padding: 0;
    font-weight: bold;
}

.features>li+li {
    margin: 10px 0 0;
}

.steps {
    display: flex;
    flex-flow: row wrap;
    align-items: stretch;
    justify-content: center;
    gap: 27px;

    list-style: none;
    margin: 0;
    padding: 0 0 20px;
}

.steps>li {
    border: 1px solid #000;
    padding: 20px;
}